<template>
  <div class="card">
    <div class="card-header"></div>
    <div class="card-body">
      <button 
        class="btn btn-success"
        @click="handleExecuteTrade"
        :disabled="loading"
      >
        {{ loading ? '...' : '' }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TradingService',
  props: {
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['execute-trade'],
  setup(props, { emit }) {
    const handleExecuteTrade = () => {
      emit('execute-trade')
    }

    return {
      handleExecuteTrade
    }
  }
}
</script>

