<template>
  <div class="card">
    <div class="card-header">Window </div>
    <div class="card-body d-flex flex-column gap-2">
      <div 
        v-for="api in apis" 
        :key="api.url" 
        class="d-flex align-items-center gap-2 flex-nowrap"
      >
        <label class="me-2 flex-shrink-0">{{ api.label }}</label>
        <input
          type="text"
          class="form-control flex-shrink-1"
          :value="api.url"
          disabled
        />
        <button
          class="btn btn-primary flex-shrink-0"
          @click="handleApiCall(api.url)"
          :disabled="loading"
        >
          {{ loading ? '...' : '' }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'WindowControl',
  props: {
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['api-call'],
  setup(props, { emit }) {
    const apis = ref([
      {
        label: '',
        url: 'http://localhost:5000/position'
      },
      {
        label: '',
        url: 'http://localhost:5000/balance'
      }
    ])

    const handleApiCall = (url) => {
      emit('api-call', url)
    }

    return {
      apis,
      handleApiCall
    }
  }
}
</script>

<style scoped>
.gap-2 {
  gap: 0.5rem;
}
</style> 